@import "../../common/styles/sass/mixins.scss";
@import "../../common/styles/sass/variables";

.skill {
    width: 250px;
    background-color: $bgSecondColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    word-break: break-all;
    text-indent: 10px;
    transition: 1s;
    opacity:0.7 ;
    z-index: 0;

    &:hover{
        -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
        transform: scale(1.3);
        cursor: pointer;
        opacity: inherit;
        z-index: 3;
        box-shadow: 2px 2px 3px;
    }

    .icon {
        width: 60px;
        height: 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        object-fit: cover;
        object-position: center;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

    }
    .skillTitle {
        @include text ($titleFontFamily,16px,1.2);
        letter-spacing: 1px;
        margin: 0 0 5px;

    }
    .description {
        @include text($descriptionFontFamily,14px,1.8);
        font-weight: 400;

    }

}






